video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.hero__container {
  background: url("../../assets/images/img-home.jpg") center center/cover
    no-repeat;
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.hero__container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -100px;
}

.hero__container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
}

.hero__buttons {
  margin-top: 32px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.hero__buttons .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero__container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
  .hero__buttons {
    flex-direction: column;
  }
}

@media screen and (max-width: 768px) {
  .hero__container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }
  .hero__container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 90%;
  }
}
